
















/* eslint-disable @typescript-eslint/no-explicit-any */

import Vue from 'vue';
import store from '@/store/store';

export default Vue.extend({
  name: 'PetLink',

  props: {
    module: String,
    id: String,
  },

  computed: {
    model(): any {
      return store.getters[this.module].filterById(this.id);
    },
  },
});
